.card-body {
  background-color: transparent;
  margin: 0 auto;
}

.cards {
  padding: 4rem;
  position: relative;
  left: 8%;
  background-color: white;
}

.cards h1 {
  text-align: center;
  background-color: white;
}

.card_container {
  display: flex;
  padding-top: 60px;
  background: transparent;
  flex-flow: column;
  align-items: center;
  width: 50%;
  margin: 0 auto;
}

.search {
  position: relative;
  left: 6vw;
  display: flex;
  width: 80vw !important;
}

.search #button {
  width: 10vw;
  margin-left: 47vw;
}

.search #outlined-basic {
  width: 12vw;
}

.card_wrapper {
  position: relative;
  margin: 4vh 2vw;
  background-color: transparent;
}

.card_items {
  margin: 0 auto;
  z-index: 1;
}

.card {
  display: flex;
  flex: 1;
  margin: 0 1.5vw;
  border-radius: 10px;
  width: 20vw;
}

.card_link {
  display: flex;
  flex-flow: column;
  width: 100%;
  box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
  -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  border-radius: 10px;
  overflow: hidden;
  text-decoration: none;
}

.fade-img {
  animation-name: fade-img;
  animation-duration: 2s;
}

.card_picture_wrapper {
  position: relative;
  width: 100%;
  padding-top: 70%;
  overflow: hidden;
}

.card_picture_wrapper::after {
  content: attr(data-category);
  position: absolute;
  bottom: 0;
  margin-left: 5vw;
  padding: 6px 8px;
  width: 5vw;
  max-width: calc((100%) - 60px);
  font-size: 12px;
  font-weight: 700;
  color: white;
  background-color: #fa6352;
  box-sizing: border-box;
}

