.card_image {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  object-fit: cover;
  transition: all 0.2s linear;
}

.card_image:hover {
  transform: scale(1.1);
}

.card_info {
  padding: 20px 30px 30px;
  background-color: white;
}

.card_text {
  color: rgb(25, 25, 25);
  font-size: 18px;
  line-height: 24px;
  background: transparent;
}

@media only screen and (min-width: 1200px) {
  .content__blog__container {
    width: 84%;
  }
}

@media only screen and (min-width: 1024px) {
  .card_items {
    display: flex;
  }
}

@media only screen and (max-width: 1024px) {
  .card {
    margin-bottom: 2rem;
  }
}